body {
  max-width: 100vw;
  height: 100vh;
}
.react{
  max-width: 100vw;
}
body{
  width: 100vw;
  height: 100vh;
  padding: 0;
  margin: 0;
  padding: 0;
  font-family: MiFue;
}
@font-face {
  font-family: SummerFaith-Regular;
  src: local('fonts'),
   url("./fonts/Summer-Faith.otf") format("opentype");
}
@font-face {
  font-family: Montserrat;
  src: local('fonts'),
   url("./fonts/Montserrat-Regular.otf") format("opentype");
}


@font-face {
  font-family: SummerFaith;
  src: local('fonts'),
   url("./fonts/Summer Faith.ttf") format("truetype");
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',SummerFaith-Regular,
    monospace,MiFuentePersonal,Montserrat;

    
}

